body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.toolbar{
  display: flex;
  justify-content: space-between;
  background-color: red;
  height: 5vh;
  /* position: absolute !important; */
  width: 100%;
  /* z-index: 1; */

}

.link{
  text-decoration: none;
  color: white;
}

.link:hover{
  color: white;
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.signupform{
  width: 50vw;
  height: 60vh;
  border-radius: 3%;
  padding:30px;
  position: absolute;
  top:20%;
  left: 25vw;
  text-align: center;
  box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
}
.signup-input{
  width: 90%;
  margin-left: 5%;
}
.half{
  display: flex;
}
.half p{
  width: 43%;
  margin-left: 5%
}
.half p:nth-child(2){
  margin-left: 4%
}

.Loginform{
  width: 50vw;
  height: 40vh;
  border-radius: 3%;
  padding:30px;
  position: absolute;
  top:20%;
  box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
  left: 25vw;
  text-align: center;
}
.drawer{
  position: relative;
}
.list-drawer{
   width: 200px;
   margin: 0;
}
.div-info{
  width: 50%;
  margin-left:5%; 
}

.card-info{
  width: 100%;
  margin-top: 30px
}
.card-info h3{
  text-align: start;
  color: red;
}
.profile-cards{
  display: flex;
  flex-wrap: wrap; 
  text-align: center; 
}

.Postform{
  width: 50vw;
  border-radius: 3%;
  box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
  padding:30px;
  position: absolute;
  top:20%;
  left: 25vw;
  text-align: center;
}
.container{
  width: 1200px;
  margin: 0 auto;
}

@media (max-width:960px){
  .container{
    width: 100%;
  }
}

.home-img{
  background-image: url(https://res.cloudinary.com/dcnqlxs8b/image/upload/v1539609972/proyecto_final/background3.jpg);
  background-size: cover;
  background-position: 50% 0%;
  width: 100vw;
  height: 90vh;
}

.home-search{
  position:absolute;
  left: 0;
  right:0;
  top:45%;
  z-index: 1;
  text-align: center;
}
.detail-view{
  display: flex;
  text-align: justify;
  margin-top: 3%;
}
.detail-view img{
  margin-bottom: 2%;
  }

.products-display{
  display: flex;
  width: 90vw;
}

.comment-box{
  width: 100%;
}

