.Postform{
  width: 50vw;
  border-radius: 3%;
  -webkit-box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
  -moz-box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
  box-shadow: 0px 0px 17px 2px rgba(0,0,0,0.3);
  padding:30px;
  position: absolute;
  top:20%;
  left: 25vw;
  text-align: center;
}
.container{
  width: 1200px;
  margin: 0 auto;
}

@media (max-width:960px){
  .container{
    width: 100%;
  }
}

.home-img{
  background-image: url(https://res.cloudinary.com/dcnqlxs8b/image/upload/v1539609972/proyecto_final/background3.jpg);
  background-size: cover;
  background-position: 50% 0%;
  width: 100vw;
  height: 90vh;
}

.home-search{
  position:absolute;
  left: 0;
  right:0;
  top:45%;
  z-index: 1;
  text-align: center;
}
.detail-view{
  display: flex;
  text-align: justify;
  margin-top: 3%;
}
.detail-view img{
  margin-bottom: 2%;
  }

.products-display{
  display: flex;
  width: 90vw;
}

.comment-box{
  width: 100%;
}

